@media only screen and ( min-width: 414px ) {
  .projects {
    .project__cards {
      // width: 80%;
      margin: 0 auto;
    }
  }
}

@media only screen and ( min-width: 768px ) {
  .project--expand {
    width: 80%;
    
  }
  .projects {
    // height: 100% !important;
    .project__cards {
      width: 100%;
      .project__card {
        // width: 60rem;
        height: 35rem;
        .project--name {
          display: none;
        }
        .project__screenshot {
          height: 40rem;
          width: 70%;
          // margin-right: 10rem;
          filter: grayscale(1) blur(.3rem);
          overflow: hidden;
          img {
            transition:  .3s ease;

          }

        }
        .project__info {
          opacity: 1;
          border: none;
          right: 0;
          width: 80%;
          align-items: flex-end;

          p {
            text-align: end;
          }
        }

        &:hover .project__screenshot{
          filter: grayscale(0) blur(0);
          opacity: 1;
          img {
            transform: scale(1.1);
          }
        }
        &:hover .project__info{
          width: 60%;
        }

        &:nth-child( even ) {
          .project__screenshot {
            position: absolute;
            right: 0;
            bottom: 0;
            
            .right--fade {
              left: -1rem;
              background: $left-fade;
            }
          }
          
          .project__info {
            position: absolute;
            left: 0;
            top: 0;
            align-items: flex-start;
            p {
              text-align: start;
            }
          }
        }
      }
    }
  }
}

@media only screen and ( min-width: 1024px ) {
  .project--expand {
    width: 70%;
  }
}



@media only screen and ( min-width: 1440px ) {
  .projects {
    height: 100%;
    .project__cards {
      .project__card {
        // overflow: hidden;
        .project--name {
          display: none;
        }
        .project__info {
          opacity: 1;
          border: none;
          right: 0;
          width: 40%;
          align-items: flex-end;

          p {
            text-align: end;
          }
        }
        &:hover .project__info{
          width: 30%;
        }
      }
    }
  }

  .project--expand  {
    height: 100%; 
    
    }
  }

  @media only screen and ( min-width: 1600px ) {
    .project--expand {
      width: 60%;
    }
  }

  @media only screen and ( min-width: 1920px ) {
    .project--expand {
      width: 50%;
    }
  }